* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
#logo-hidden {
  display: none;
}

.logo {
  display: block !important;
  max-width: 10vw;
  position: absolute;
  top: 20%;
  left: 10%;
}

/* NAVIGATION */
nav {
  position: fixed;
  background: transparent;
  top: 0%;
  height: 80px;
  display: flex;
  align-content: space-between;
  justify-content: center;
}

.nav-list-container {
}
.Nav__black {
  z-index: 2;
  background: #1e1e1e;
  width: 100%;
  height: 12vh;
}

nav .nav-ul {
  list-style: none;
  /* position: absolute;
  margin-bottom: 0px;
  margin-left: 850px; */
  width: 120vw;
  display: flex;
  align-content: space-between;
  align-items: flex-end;
  padding-left: 45%;
  padding-top: 15px;
}

nav .nav-ul .nav-list {
  display: inline-block;
  margin: 12px;
  margin-top: 20px;
}
nav .nav-ul .nav-list .nav-index {
  display: block;
  position: relative;
  text-decoration: none;
  color: rgb(247, 245, 245);
  font-weight: 500;
  font-size: 18px;
  padding: 5px;
  font-family: "Alata", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
}

