body {
  color: #202020;
  overflow-x: hidden;
}

.header-img {
  position: absolute;
  top: 0%;
  width: 100%;
  height: 100vh;
  background: url(/trim/static/media/pexels-bri-schneiter-346529.8a3ffb63.jpg);
  background-size: cover;
  z-index: -1;
}

.header-content {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo-main {
  max-width: 18vw;
  margin: 3vw;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.75);
}

.cover-h2 {
  /* position: absolute;
  left: 10%;
  top: 35%; */

  font-family: Montserrat;
  font-weight: 600;
  line-height: 10vh;
  color: rgb(253, 250, 250);
  font-size: 3vw;
  width: 35rem;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

