.appoint-button {
  /* position: absolute;
  top: 71%;
  left: 10%; */
  margin-top: 5vh;
  background-color: #f4f4f4;
  font-size: 1.2vw;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  width: 12vw;
  height: 3.5vw;
  border-radius: 10px;
  /* text-shadow: 0px 2px rgba(77, 77, 77, 0.5); */
  /* box-shadow: 0px 6px rgba(77, 77, 77, 1); */
  cursor: pointer;
}
.appoint-button:hover {
  transform: scale(1.05);
}

.appoint-button:active {
  text-shadow: 0px 2px rgba(77, 77, 77, 0.5);
  box-shadow: 0px 0px rgba(77, 77, 77, 1);
  transform: translateY(6px);
}

.quotes {
  font-family: "Baskervville", serif;
  font-size: 1.5vw;
  font-weight: lighter;
  line-height: 50px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 8vw;
}

.beard-icon {
  width: 6vw;
  margin-bottom: 6vw;
}

.qoute-1 {
  max-width: 50vw;
}

.qoute-line {
  margin-top: 6vw;
  height: 5px;
  width: 100%;
  border-radius: 10px;
  background-color: #1e1e1e;
}

.header-cover-1 {
  margin-top: 12vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 0%;
  position: relative;
}

#button-2 {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header-img-1 {
  width: 100%;
}

@media screen and (max-width: 1000px) {
  * {
    overflow-x: hidden;
  }
  .logo-main {
    max-width: 40vw;
  }
  .cover-h2 {
    font-size: 5vw;
    width: 40rem;
    line-height: 8vh;
  }
  .appoint-button {
    border-radius: 5px;
    width: 18vw;
    height: 7vw;
    font-size: 2.5vw;
  }
  .quotes {
    font-size: 3vw;
    line-height: 45px;
    margin-top: 12vw;
  }
  .beard-icon {
    width: 12vw;
  }
  .qoute-1 {
    max-width: 90vw;
  }
  .qoute-line {
    margin-top: 12vw;
    height: 3px;
  }
}

@media screen and (max-width: 500px) {
  * {
    overflow-x: hidden;
  }
  .header-img {
    height: 90vh;
  }
  .logo-main {
    max-width: 50vw;
  }
  .cover-h2 {
    font-size: 8vw;
    width: 20rem;
    line-height: 6vh;
  }
  .appoint-button {
    border-radius: 5px;
    width: 20vw;
    height: 8vw;
    font-size: 2.5vw;
  }
  .quotes {
    font-size: 3vw;
    line-height: 20px;
    margin-top: 12vw;
  }
  .beard-icon {
    width: 12vw;
  }
  .qoute-1 {
    max-width: 80vw;
  }
  .qoute-line {
    margin-top: 12vw;
    height: 3px;
  }
}

.products {
  margin-top: 8vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-h1 {
  font-family: "Alata", sans-serif;
  font-size: 3.5vw;
  margin-bottom: 3vh;
}

.product-list {
  display: flex;
  flex-direction: row;
}

.product-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 6vw 4vw;
}
.product-img {
  max-width: 20vw;
}

.product-name {
  font-family: "Alata", sans-serif;
  font-size: 2.5vw;
  text-align: center;
}

.product-price {
  margin-top: 1vh;
  font-size: 1.8vw;
}

@media screen and (max-width: 1000px) {
  .product-img {
    max-width: 25vw;
  }
  .product-h1 {
    font-size: 6vw;
  }

  .product-name {
    font-size: 5vw;
  }
  .product-price {
    margin-top: 0.5vh;
    font-size: 4vw;
  }
}

@media screen and (max-width: 500px) {
  .product-img {
    max-width: 50vw;
  }
  .product-h1 {
    font-size: 8vw;
  }
  .product-list {
    flex-direction: column;
  }
  .product-name {
    font-size: 6vw;
  }
  .product-price {
    margin-top: 0vh;
    font-size: 5vw;
  }
}


.nav-box {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 40vh;
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.75);
  width: 100vw;
  position: relative;
  z-index: -2;
  background: #1e1e1e;
  color: white;
}

.nav-box-text {
  font-size: 4vw;
  margin: 3vw;
  font-family: Roboto;
}

#gallery-nav-box {
  background-image: url(/trim/static/media/pexels-mostafa-meraji-3037244.413bfa77.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.gallery-img-container {
  width: 80vw;
  margin: 5vw 10vw 5vw 10vw;
}

.gallery {
}

@media screen and (max-width: 1000px) {
  .nav-box {
    height: 20vh;
    background-position-x: 50%;
  }
  .nav-box-text {
    font-size: 6vw;
  }
  .gallery-img-container {
    width: 90vw;
    margin: 5vw;
  }
}
@media screen and (max-width: 500px) {
  .nav-box {
    height: 20vh;
    background-position-x: 50%;
  }
  .nav-box-text {
    font-size: 8vw;
  }
  .gallery-img-container {
    width: 90vw;
    margin: 5vw;
  }
}

.about-container {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Roboto;
  font-size: 1.5vw;
}

.barbers {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0vw 0vw 3vw 0vw;
}

.barber-1,
.barber-2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5vw;
}

.barber-info {
  display: flex;
  flex-direction: column;
  width: 40vw;
  margin-left: 2vw;
}

.barber-img {
  max-width: 20vw;
}

.barber-name {
  font-size: 3vw;
  margin-bottom: 0.5vw;
}

.company-history {
  text-align: center;
  max-width: 70vw;
  text-align: center;
  margin-top: 8vw;
}

.company-history-h2 {
  font-size: 4vw;
}
.company-history-p {
  margin: 2vw 0px 2vw 0vw;
  font-size: 2vw;
}

#about-qoute-line {
  max-width: 70vw;
}

.row { /* IE10 */
  display: flex;
  align-content: center;
  margin: 2vw 0vw 5vw 0vw;
  justify-content: center; /* IE10 */
  flex-wrap: wrap;
  padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column { /* IE10 */
  flex: 25% 1;
  max-width: 25%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 500px) {
  .barbers {
    margin: 3vw 0vw 0vw 0vw;
  }

  .barber-info {
    width: 50vw;
  }

  .barber-img {
    max-width: 30vw;
    margin-right: 3vw;
  }

  .barber-name {
    font-size: 5vw;
    margin-bottom: 0.5vw;
  }

  .barber-details {
    font-size: 2.5vw;
  }

  .company-history {
    margin-top: 6vw;
  }

  .company-history-h2 {
    font-size: 6vw;
  }
  .company-history-p {
    font-size: 2.5vw;
  }

  .column {
    flex: 50% 1;
    max-width: 50%;
  }
  .about-gallery {
    margin: 0vw 2vw 0vw 2vw;
  }
}

footer {
  margin-top: 0%;
  height: auto;
  display: flex;
  height: 35vh;
  align-self: flex-end;
  align-items: center;
  justify-content: space-around;
  background: #1e1e1e;
  color: white;
  font-family: "Alata", sans-serif;
  font-size: 1.5vw;
}

.footer-logo {
  height: 5vw;
  margin-bottom: 2vh;
}

.social-media-logo {
  height: 5vh;
  margin: 0px 6px;
}

.footer-hours,
.footer-location {
  max-width: 15vw;
  text-align: center;
}

@media screen and (max-width: 1000px) {
  footer {
    height: 18vh;
    font-size: 2vw;
  }
  .footer-logo {
    height: 8vw;
    margin-bottom: 1vh;
  }
  .social-media-logo {
    height: 2.5vh;
    margin: 0px 4px;
  }
  .footer-hours,
  .footer-location {
    max-width: 30vw;
  }
}

@media screen and (max-width: 500px) {
  footer {
    height: 15vh;
    font-size: 2.5vw;
  }
  .footer-logo {
    height: 10vw;
    margin-bottom: 1vh;
  }
  .social-media-logo {
    height: 2vh;
    margin: 0px 2px;
  }
  .footer-hours,
  .footer-location {
    max-width: 25vw;
  }
}

